import React from 'react'
import logo from '../logo.png'
const Chat = (props) => 
{
	return(
		<React.Fragment>
			<nav className="navbar navbar-light bg-red">
				    <span className="navbar-brand">
				    	<img src={logo} className="l-size" alt="Narven logo" />
				    </span>
				    <h4><i onClick={() => props.history.push('/')} className="fas fa-plus plus skip-btn"></i></h4>
			</nav>

			<div className="container">
				<div className="row">
					<div className="chat"></div>
				</div>
			</div>

			<footer className="fixed-bottom d-inline-block align-right">
				<div className="float-left w-70">
					<input type="text" className="form-control display-7" id='message' placeholder="Type a message" />
				</div>
				<div className="send-container">
					<i className="fas fa-paper-plane send-btn"></i>
					<i className="fas fa-camera img-btn"></i>
				</div>
			</footer>
		</React.Fragment>
		);
}

export default Chat