import React from 'react'

const Main = (props) => 
{
	return(
		<React.Fragment>
		<div className="container center">
		<div className="row justify-content-center pd-b">
			<div className="col title">
				<h2 className="h-size">Narven.</h2><br />
				<h3 className="t-size">Pick a nickname and start<br /> talking with strangers.</h3>
			</div>
		</div>
		<div className="row justify-content-center">
			<div className="col-md-4 form-group">
				<label  className="form-control-label">Nickname:</label>
				<input type="text" required="required" className="form-control" />
				<div className="pt_10">
					<input type="button" onClick={() => props.history.push('/chat')} className="form-control start-btn" value="Start" />
				</div>
			</div>
		</div>
		</div>
		<footer className="fixed-bottom row justify-content-center">
			<p className="t-size f-text">Created by <span className="r-text">AF</span>.</p>
		</footer>
		</React.Fragment>
	);
}

export default Main